import { useState } from 'react';

import i1 from './images/1.JPG';
import i2 from './images/2.JPG';
import i3 from './images/3.JPG';
import i4 from './images/4.JPG';

import Accordion from './components/Accordion/Accordion';
import AccordionItem from './components/Accordion/AccordionItem/AccordionItem';
import Slider from './components/Slider/Slider';
import SliderItem from './components/Slider/SliderItem/SliderItem';

const initialData = [
  {
    img: i1,
    text: 'Oboohitch loh',
    bgColor: 'bg-red-500',
  },
  {
    img: i2,
    text: 'Oboohitch loh',
    bgColor: 'bg-yellow-500',
  },
  {
    img: i3,
    text: 'Oboohitch loh',
    bgColor: 'bg-blue-500',
  },
  {
    img: i4,
    text: 'Oboohitch loh',
    bgColor: 'bg-green-700',
  },
];

function App() {
  console.log();
  const [data, setData] = useState([...initialData]);
  // let [slide, setSlide] = useState(0);
  // console.log(slide);
  return (
    <>
      <div className='bg-green-500 w-full min-h-screen flex justify-center items-center flex-col'>
        <button
          className=''
          onClick={() =>
            setData([
              ...data,
              { img: i4, text: 'Oboohitch loh', bgColor: 'bg-green-700' },
            ])
          }
        >
          Add
        </button>
        <Accordion
          config={{
            width: '80%',
            transitionDuration: '300ms',
            activeItemWidth: '75%',
          }}
        >
          {data.map((item, index) => (
            <AccordionItem key={index}>
              {({ isActive }) => {
                return (
                  <div
                    className={`flex items-center text-2xl text-white justify-center h-125 ${item.bgColor}`}
                  >
                    {isActive ? (
                      item.text
                    ) : (
                      <img
                        className='select-none'
                        src={item.img}
                        alt=''
                        width='80'
                        height='80'
                      />
                    )}
                  </div>
                );
              }}
            </AccordionItem>
          ))}
        </Accordion>
      </div>
      <div className='bg-green-300 w-full min-h-screen flex justify-center items-center flex-col'>
        <Slider>
          {data.map((item) => (
            <SliderItem>
              <div
                className='h-125 bg-cover bg-center bg-no-repeat'
                style={{ backgroundImage: `url('${item.img}')` }}
              ></div>
            </SliderItem>
          ))}
          {/* <SliderItem>1</SliderItem>
          <SliderItem>2</SliderItem>
          <SliderItem>3</SliderItem>
          <SliderItem>4</SliderItem> */}
        </Slider>
      </div>
    </>
  );
}

export default App;
